import React from "react";
import {Grid, Card} from "@material-ui/core";

import Follow from './Components/Follow';
import AuthorInfo from './Components/AuthorInfo';
import Content from '../EachCard/Components/Content';
import Buttons from '../EachCard/Components/Buttons';

import { withStyles } from "@material-ui/core";
let styles = theme=>({
    content: {
        paddingTop: '0'
    }
});

let info = {
    title: '?',
    to: '/home',
    peple: 'somebody',
    content: `start:-1 -2 end: start `,
    like: '9990',
    comment: '9999',
    type: 'answer',
    imgsrc: ''
}

let TODO = {
    TODO:[
        "1. "
    ]
}

class FromFollow extends React.Component{
    content = {
        time: '10',
        nobody: true
    }

    mokeData = (info)=>{
        let conf = [];
        for(let i=0; i<10; i++){
            conf.push(info);
        }
        return conf;
    }
    handleClick = {
        content: ()=>{

        }
    }

    render(){
        let {classes} = this.props;
        return this.mokeData(info).map((item, key)=>{
            return <Card className={this.props.classes.root} key={key}>
                <Follow content={this.content}/>
                <AuthorInfo content={this.content}/>
                {/*  */}
                <Content content={item} handleClick={this.handleClick.content} root={classes.content}/>

                {/* Buttons */}
                <Buttons info={item}/>
            </Card>
        })
    }
}

export default withStyles(styles)(FromFollow);