let styles = theme=>({
    
})

let info = {
    title: '?',
    to: '/home',
    peple: 'somebody',
    content: `start:-1 -2 end: start `,
    like: '9990',
    comment: '9999',
    type: 'answer',
    imgsrc: ''
}

class EachCard extends React.Component{
    handleClick = {
        content: (e)=>{
            //
        },
    }
    render(){
        let {classes} = this.props;
        let content = this.mokeData(info);

        return this.renderCard(content)
    }

    mokeData = (info)=>{
        let conf = [];
        for(let i=0; i<10; i++){
            conf.push(info);
        }

        return conf;
    }

    renderCard = (info)=>{

        return info.map((item, key)=>{
            return <Card key={key}>
                {/*  */}
                <Content content={item} handleClick={this.handleClick.content}/>

                {/* Buttons */}
                <Buttons info={item}/>
            </Card>
        })
    }
}
export default withStyles(styles)(EachCard);